body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    font-family: 'Helvetica', 'Arial', sans-serif;
}

/* overwrite default modal-header styles */
.modal-header {
    background-color: #ff5d1d;
    padding: 0;
}

/* overwrite default close button in modal header*/
button.close {
    padding: 10px;
}

#modal-logo {
    display: block;
    margin: auto;
    width: 100px;
}

/********** all the styles ABOVE the orange div ***********/
.modal-top-details {
    display: flex;
    flex-direction: row-reverse;
}

    .modal-icon-div {
        flex: 1 1;
        margin: 50px;
    }

        .modal-top-details .modal-star-icon {
            width: 40px;
            height: 40px;
        }

    .modal-detail-text {
        margin: 30px 0px 30px 30px;
        flex: 2 1;
    }

        .modal-year-make {
            font-size: 24px;
            color: #6b6b6b;
        }

        .modal-model-trim {
            font-size: 30px;
            font-weight: bold;
        }

        .modal-miles {
            font-size: 20px;
            color: #ff4e00c7;
        }

/****** the orange center div *******/
.modal-center-div {
    background-color: #ff5d1d;
    height: 14px;
}

/********* all styles for elements BELOW the orange div **********/

.modal-payment-details {
    margin: 30px 0px 20px 30px;
    display: flex;
    flex-direction: row;
}
    .modal-monthly-div {
        flex: 1 1;
    }
        .modal-payment-headers {
            font-size: 22px;
        }

        .modal-monthly-fee {
            font-size: 50px;
            color: #ff5d1d;
        }

    .modal-start-div {
        flex: 2 1;
    }
        .modal-start-fee {
            font-size: 50px;
        }
#footer-cars {
    height: 100px;
    margin-bottom: -230px;
    margin-left: 10%;
    overflow: hidden;
}

.footer {
    background-color: #ff4800;
    bottom: 0;
    height: 150px;
    flex-direction: column;
    flex: 3 1;
}
* {
    font-family: 'Helvetica', 'Arial', sans-serif;
}

#logo-top {
    display: block;
    margin: 50px auto;
    width: 200px;
}

.listing-body {
display: flex;
min-height: 100vh;
flex-direction: row;
}

/******* All the filters styles *******/

.listing-filters {
    flex: 1 1;
}

/* filter headers */
th {
    background-color: #ff5e1ff2;
    font-size: 18px;
    color: #ffffff;
}

/* overwrite default slider color*/
.rangeslider-horizontal .rangeslider__fill {
    background-color:  #ff7843;
}

/******* All the car detail styles *******/
.listing-cars {
    flex: 2 1
}

.car-listing {
    cursor: pointer;
}

.td-car-image {
    text-align: center; 
    vertical-align: middle;
}
    .car-listing-image {
        width: 300px;
    }

/* overwrite default table alignment */
.table>tbody>tr>td {
    vertical-align: middle;
}
    /* overwrite default font awesome icon*/
    .svg-inline--fa.fa-w-18 {
        margin-left: 85%;
        font-size: 20px;
        color: #ff7843;
        opacity: .2;
    }
    .svg-inline--fa.fa-w-18:hover {
        margin-left: 85%;
        font-size: 20px;
        color: #ff7843;
        opacity: 100;
    }
    .car-header {
        font-size: 16px;
        font-weight: bold;
    }

    .car-listing-details {
        font-size: 14px;
        color: #9f9f9f;
    }

/* text that appears if no cars match the filters */
#listing-sorry-text {
    font-weight: bold;
}

/******* All Pagination Styles *******/

/* overwrite default pagination color */
.pagination>.active>span, .pagination>.active>span:hover {
    background-color: #ff4800;
    border-color: #e62b00;
}

.pagination>li>a, .pagination>li>span, .pagination>li>a:hover {
    color: #ff4800;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

